/* solution */
.solution {
  width: 100%;
  height: auto;
  margin-bottom: 0.5vw;
}
.solution .text_box {
  width: 100%;
  height: 7vw;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
}
.solution .text_box p {
  font-size: 1.6vw;
  font-weight: 600;
}
.solution .article_container {
  width: 100%;
  height: auto;
  margin-top: 1vw;
  display: flex;
  justify-content: space-between;
}
.solution .article_container .nav {
  width: 12%;
  height: 25vw;
  border: 1px solid black;
  padding: 0 1vw;
}
.solution .article_container .nav p:nth-child(1) {
  font-size: 1.4vw;
  font-weight: 600;
  margin-top: 2vw;
  margin-bottom: 2vw;
  color: blue;
}
.solution .article_container .nav hr {
  margin-bottom: 1.5vw;
}
.solution .article_container .nav > p {
  font-size: 1.2vw;
  margin-bottom: 1vw;
}
.solution .article_container .nav p:nth-child(3) {
}
.solution .article_container .article {
  width: 87%;
  height: auto;
  border: 1px solid black;
}
.solution .article_container .article img {
  width: 100%;
  height: fit-content;
}
@media screen and (max-width: 500px) {
  .solution {
    margin-bottom: 3vw;
  }
  .solution .text_box {
    height: 10vw;
  }
  .solution .text_box p {
    font-size: 2.5vw;
  }
  .solution .article_container .nav {
    width: 20%;
    height: 50vw;
    border-right: none;
  }
  .solution .article_container .article {
    width: 80%;
  }
  .solution .article_container .nav p:nth-child(1) {
    font-size: 3vw;
    margin-top: 5vw;
    margin-bottom: 3vw;
  }
  .solution .article_container .nav hr {
    margin-bottom: 3vw;
  }
  .solution .article_container .nav > p {
    font-size: 2vw;
    margin-bottom: 2vw;
  }
}
